import React, { Component} from 'react';
import FormFieldsRegistration from '../methodes/formFieldsRegistration';
import {validate} from '../methodes/validate';




class Registration extends Component {
  
  state = {
    loading: false,
    formData: {
      brand:{
        element:'select',
        value:'',
        config:{
          name:'brand_input'
        },
        validation:{
          required:true
        },
        valid:false,
        touched:false,
        validationMessage:''
      },
      power:{
        element:'input',
        value:'',        
        config:{
            name:'power_input',
            type:'',          
            placeholder:'Enter your KS',
            maxLength: '3'
                      
        },
        validation:{
            required:true
        },
        valid:false,
        touched: false,
        validationMessage:''
    },
    }
  }
///Brand field select
  generateOptions = () => {
    const brandArray = ['BMW', 'Audi','Mercedes'];
    
    return brandArray.map((value,id)=>(
      <option key={id} value={value}>{value}</option>
    ))
  }

  updateForm = (element) => {
    const newFormData = { ...this.state.formData }
    const newElement = { ...newFormData[element.id] }

    newElement.value = element.event.target.value;

    ///Validation
    let validateData = validate(newElement);
        newElement.valid = validateData[0];
        newElement.validationMessage = validateData[1];

///if the field was touched
    if(element.blur){
      newElement.tuched = element.blur
    }
///state
    newFormData[element.id] = newElement;
        this.setState({
            formData: newFormData
        })
  }

///Submit
submitForm = (event) => {
  event.preventDefault();

  let dataToSubmit = {};
  let formIsValid = true;

  for(let key in this.state.formData){
      formIsValid = this.state.formData[key].valid && formIsValid;
  }
  if(formIsValid){
      this.setState({loading:true});
      for(let key in this.state.formData){
          dataToSubmit[key] =  this.state.formData[key].value
      }
      // console.log('SUBMIT',dataToSubmit);
      setTimeout(() => {
          this.setState({loading:false});
          this.onSuccess();
      }, 2000);


  } else {
      alert('Form is not valid')
  }
}

onSuccess = () => {
  let forDataCopy = {
      ...this.state.formData
  }

  for(let key in this.state.formData){
      forDataCopy[key].value = '';
      forDataCopy[key].valid = false;
      forDataCopy[key].touched = false;
      forDataCopy[key].validationMessage = '';
  }
  this.setState({formData: forDataCopy});
  alert('Thank You')
}
  render(){
    return (
      <>    
          <div className='container'>
              <form>
              
                <div className='form-group'>
                
                    <label>Brand of car</label>
                    
                    <FormFieldsRegistration
                      formData={this.state.formData.brand}
                      change={ (element) => this.updateForm(element)}
                      id='brand'
                    >                          
                          <option value=''>Select brand of auto</option>
                          {this.generateOptions()}
                          
                    </FormFieldsRegistration>

                </div>

                <div className="form-group">
                            <label>Power</label>
                            <FormFieldsRegistration
                                formData={this.state.formData.power}
                                change={ (element) => this.updateForm(element) }
                                id="power"
                            />
                </div>

                <button
                            type="submit"
                            className="btn btn-primary"
                            onClick={ (event)=> this.submitForm(event) }
                            disabled={this.state.loading}
                np>
                Submit
                </button>

              </form>


          </div>
      </>

      
          
    )
  }
}

export default Registration;